<template>
  <div>
    <v-card flat>
      <v-toolbar flat color="primary" dark>
        <v-toolbar-title>Mission Microwave</v-toolbar-title>
      </v-toolbar>
        
      <v-form @submit.prevent="authenticateCredential">
        <v-row>
          <v-col cols="4"> </v-col>
          <v-col cols="4">
            <v-text-field label="Username" required
              v-model="userName" :rules="[IsValid(userName)]">
            </v-text-field>
          </v-col>
          <v-col cols="4"> </v-col>
        </v-row>

        <v-row>
          <v-col cols="4"> </v-col>
          <v-col class="text-body-1 d-flex">
            <v-text-field label="Password" required
              :type="passIsVisible ? 'text' : 'password'" :append-icon="passIsVisible ? 'mdi-eye' : 'mdi-eye-off'" @click:append="passIsVisible = !passIsVisible"
              v-model="password" :rules="[IsValid(password)]">
            </v-text-field>
          </v-col>
          <v-col cols="4"> </v-col>
        </v-row>

        <v-row>
          <v-col md="4"> </v-col>
          <v-col md="4">
            <v-btn :disabled="IsValid(userName) == false || IsValid(password) == false" type="submit">
              Login
            </v-btn>
          </v-col>
          <v-col md="4"> </v-col>
        </v-row>
      </v-form>

    </v-card>
  </div>
</template>


<script>

export default {
  name: 'Login',

  data: function() {
    return {
      passIsVisible : false,
      userName: "",
      password: "",
    }
  },

  methods: {
    IsValid(input) {
      return input.length > 0;
    },

    authenticateCredential(event) {
      event.preventDefault();
      var userCredential = this.userName + "_" + this.password;
      sessionStorage.setItem('credential', userCredential);

      if (sessionStorage.getItem('credential') != null) {
        this.$router.push('/dashboard');
      }
    }
  }

}
</script>

